import { Loading } from 'element-ui'

let loadingInstance

export function startLoading(targetdq) {
    let options = {
        text: 'loading......',
        background: 'rgba(255, 255, 255, 0.6)',
        target: document.querySelector(targetdq) || 'body',
        customClass: 'server_loading'
    }
    loadingInstance = Loading.service(options)
}

export function endLoading() {
    loadingInstance.close()
}

